:root {
  --font-size-small: 1.2rem;
  --font-size-small-1: 1.4rem;
  --font-size-normal: 1.6rem;
  --font-size-normal-1: 1.8rem;
  --font-size-medium: 2rem;
  --font-size-medium-1: 3rem;
  --font-size-medium-2: 4rem;
  --font-size-medium-3: 5rem;
  --font-size-large: 6rem;
  --font-size-huge: 8rem;
  --font-size-giant: 12rem;
  --font-size-ultimate: 15rem;

  --line-height-normal: 1.7;
  --line-height-small: 1.3;

  --black: #222831;
  --pure-black: #000;
  --blue: #0172b6;
  --white: #f9f7f7;
  --pure-white: #fff;
  --red: #d8303c;
  --text-color: #071e54;

  --container-max-width: 1900px;
  --container-normal-width: 600px;
  --container-medium-width: 700px;
  --container-small-width: 500px;

  --text-shadow: 2px 2px 3px #000000;
  --text-shadow-1: 2px 1px 2px #000000;

  --poppins: 'Poppins', sans-serif;
  --quote: 'Secular One', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%; /*(16/10)*100 = 62.5% => 1rem = 10px*/
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.main_footer {
