.change_content:after {
  content: '';
  -webkit-animation: changetext 15s infinite linear;
          animation: changetext 15s infinite linear;
}

@-webkit-keyframes changetext {
  0% {
    content: 'LIC INSURANCE...';
  }
  10% {
    content: 'HEALTH INSURANCE...';
  }
  18% {
    content: 'VEHICLE INSURANCE...';
  }
  26% {
    content: 'ACCIDENTAL INSURANCE...';
  }
  34% {
    content: 'TRAVEL INSURANCE...';
  }
  42% {
    content: 'PRODUCT INSURANCE...';
  }
  50% {
    content: 'MARINE INSURANCE...';
  }
  57% {
    content: 'COMPANY INSURANCE...';
  }
  64% {
    content: 'OFFICE INSURANCE...';
  }
  71% {
    content: 'HOTEL INSURANCE...';
  }
  78% {
    content: 'HOUSE INSURANCE...';
  }
  85% {
    content: 'SHOP INSURANCE...';
  }
  92% {
    content: 'FLAT INSURANCE...';
  }
  100% {
    content: 'FINANCIAL INSURANCE...';
  }
}

@keyframes changetext {
  0% {
    content: 'LIC INSURANCE...';
  }
  10% {
    content: 'HEALTH INSURANCE...';
  }
  18% {
